        padding-top: .6rem
    }
  }
  
  .footer-link-01 a {
    position: relative;
    display: inline-block;
    vertical-align: top
  }
  
  .footer-link-01 a:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: auto;
    right: 0;
    width: 0;
    height: 1px;
    transition: ease all .35s;
    background: currentColor
  }
  
  .footer-link-01 a:hover:after {
    left: 0;
    right: auto;
    width: 100%
  }
